import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  FloatingLabel,
} from "react-bootstrap";

function Register() {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [nickname, setNickName] = useState("");
  const [password, setPassword] = useState("");
  const [passwordCheck, setPasswordCheck] = useState("");

  const onEmailHandler = (e) => {
    setEmail(e.target.value);
  };
  const onNameHandler = (e) => {
    setName(e.target.value);
  };
  const onNickNameHandler = (e) => {
    setNickName(e.target.value);
  };
  const onPasswordHandler = (e) => {
    setPassword(e.target.value);
  };
  const onPasswordCheckHandler = (e) => {
    setPasswordCheck(e.target.value);
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();

    if (password !== passwordCheck) {
      return setPasswordCheck("");
    }

    const body = {
      email,
      name,
      nickname,
      password,
      passwordCheck,
    };

    axios
      .post("http://localhost:8086/member/join", body)
      .then((res) => {
        console.log(res);
        if (res.data.success) {
          console.log("회원가입 성공!");
          navigate("/login");
        } else {
          alert(res.data.message);
        }
      })
      .catch((err) => {
        alert(err.response.data.message);
      });
  };

  const goLogin = () => {
    navigate("/login");
  };

  return (
    <Container fluid className="main">
      <div className="head mgbt">회원가입</div>
      <Form onSubmit={onSubmitHandler}>
        <FloatingLabel
          controlId="floatingInput"
          label="이메일"
          className="mb-3"
        >
          <Form.Control
            type="email"
            placeholder="Enter email"
            value={email}
            onChange={onEmailHandler}
            required
          />
        </FloatingLabel>
        <FloatingLabel
          controlId="floatingInput"
          label="비밀번호"
          className="mb-3"
        >
          <Form.Control
            type="password"
            placeholder="Password"
            value={password}
            onChange={onPasswordHandler}
            required
          />
        </FloatingLabel>
        <FloatingLabel
          controlId="floatingInput"
          label="비밀번호 재입력"
          className="mb-3"
        >
          <Form.Control
            type="password"
            placeholder="Password Check"
            value={passwordCheck}
            onChange={onPasswordCheckHandler}
            required
          />
        </FloatingLabel>
        <Row>
          <Col xs="5">
            <FloatingLabel
              controlId="floatingInput"
              label="이름"
              className="mb-3"
            >
              <Form.Control
                type="text"
                placeholder="Enter name"
                value={name}
                onChange={onNameHandler}
              />
            </FloatingLabel>
          </Col>
          <Col xs="7">
            <FloatingLabel
              controlId="floatingInput"
              label="닉네임"
              className="mb-3"
            >
              <Form.Control
                type="text"
                placeholder="Enter nickname"
                value={nickname}
                onChange={onNickNameHandler}
                required
              />
            </FloatingLabel>
          </Col>
        </Row>
        <Button variant="primary" type="submit" className="mgtp">
          제출
        </Button>
      </Form>
      <p />
      <Button variant="link" type="button" onClick={goLogin}>
        이미 계정이 있나요?
      </Button>
    </Container>
  );
}

export default Register;
